import {
  MdxExtracts,
  getMDXOptions,
  remarkMdxRemoveJs,
  remarkExpandContent,
  remarkSplitCodeGroup,
  remarkSplitTabs,
} from '@mintlify/common';
import { serialize, SerializeOptions, SerializeSuccess } from '@mintlify/mdx';
import { MintConfig, PageMetaTags } from '@mintlify/models';
import { DocsConfig } from '@mintlify/validation';
import type { PluggableList } from 'unified';

import { allowedComponents } from '@/components';
import { PageType } from '@/types/page';
import { Snippet } from '@/types/snippet';
import createSnippetTreeMap from '@/utils/createSnippetTreeMap';

type GetMdxType = {
  mdxSource: SerializeSuccess;
  mdxSourceWithNoJs: SerializeSuccess;
  mdxExtracts: MdxExtracts;
  panelMdxSource?: SerializeSuccess;
  panelMdxSourceWithNoJs?: SerializeSuccess;
};

export const getMdx = async (
  path: string,
  content: string,
  metadata: PageMetaTags,
  snippets: Snippet[],
  subdomain: string,
  config: DocsConfig | MintConfig,
  tailwindSelectors: string[],
  pageType: PageType = 'default'
): Promise<GetMdxType> => {
  const snippetTreeMap = await createSnippetTreeMap(snippets);
  const mdxOptionsData = {
    subdomain,
    snippetTreeMap,
    allowedComponents,
    pageMetadata: metadata,
    config,
    tailwindSelectors,
  };

  let mdxExtracts: MdxExtracts = {};
  let plugins: PluggableList = [];
  if (pageType === 'pdf') {
    plugins = [remarkExpandContent, remarkSplitCodeGroup, remarkSplitTabs];
  }
  const mdxOptions = getMDXOptions({ data: mdxOptionsData, remarkPlugins: plugins, mdxExtracts });
  const mdxOptionsNoJs = getMDXOptions({
    data: mdxOptionsData,
    remarkPlugins: [remarkMdxRemoveJs, ...plugins],
  });

  const scope = {
    config,
    pageMetadata: metadata,
  };
  const mdxSource = await getCompiledMdxWithFallback(content, scope, mdxOptions, path);
  const mdxSourceWithNoJs = await getCompiledMdxWithFallback(content, scope, mdxOptionsNoJs, path);

  let panelMdxSource: SerializeSuccess | undefined;
  let panelMdxSourceWithNoJs: SerializeSuccess | undefined;

  const originalMdxExtracts = structuredClone(mdxExtracts);

  if (mdxExtracts.panel && mdxExtracts.panel.content) {
    const panelContent = mdxExtracts.panel.content;
    panelMdxSource = await getCompiledMdxWithFallback(panelContent, scope, mdxOptions, path);
    panelMdxSourceWithNoJs = await getCompiledMdxWithFallback(
      panelContent,
      scope,
      mdxOptionsNoJs,
      path
    );

    // serializing mdx content with the panel content will remove the original mdxExtracts
    // so we need to restore the original mdxExtracts
    mdxExtracts = {
      ...originalMdxExtracts,
      codeExamples: mdxExtracts.codeExamples,
    };
  }

  return {
    mdxExtracts,
    mdxSource,
    mdxSourceWithNoJs,
    panelMdxSource,
    panelMdxSourceWithNoJs,
  };
};

const getCompiledMdxWithFallback = async (
  source: string,
  scope: Record<string, unknown>,
  mdxOptions: SerializeOptions['mdxOptions'],
  path?: string
): Promise<SerializeSuccess> => {
  try {
    const result = await serialize({
      source,
      scope,
      mdxOptions,
    });

    if ('error' in result) {
      throw result.error;
    }

    return result;
  } catch (err) {
    if (path) console.log(`⚠️ Warning: MDX failed to parse page ${path}: `, err);
    // placeholder content for when there is a syntax error.
    const fallbackResult = await serialize({
      source:
        '🚧 A parsing error occured. Please contact the owner of this website. They can use the Mintlify CLI to test this website locally and see the errors that occur.',
      scope,
      mdxOptions,
    });

    if ('error' in fallbackResult) {
      throw fallbackResult.error;
    }

    return fallbackResult;
  }
};
