import { getFirstPageFromNavigation } from '@mintlify/common';
import { DecoratedNavigationConfig } from '@mintlify/validation';

import { getFirstPageStartingWith } from './getFirstPageStartingWith';

// Function assumes callsite validated input is a valid array
export function pickRedirect(
  docsNavWithMetadata: DecoratedNavigationConfig,
  path: string,
  disableRedirect?: boolean
): { destination: string; permanent: boolean } | undefined {
  // Allow linking to a folder and redirecting to the first page in it.
  // Eg. The path "updates/changelog" can redirect to "updates/changelog/2022" but not "updates/changelog-page"
  const firstPageInSubdir = getFirstPageStartingWith(docsNavWithMetadata, '/' + path + '/');
  if (firstPageInSubdir) {
    return {
      destination: firstPageInSubdir.href,
      permanent: false,
    };
  }

  // If disableRedirect is true, do not redirect to home page
  if (disableRedirect) return;

  // Redirect to the home page
  const firstPage = getFirstPageFromNavigation(docsNavWithMetadata);
  if (firstPage !== undefined) {
    return {
      destination: firstPage.href || '/',
      permanent: true,
    };
  }
}
