import { isWildcardRedirect } from '@mintlify/common';
import { DecoratedNavigationConfig, DocsConfig } from '@mintlify/validation';

import { isWildcardRedirectSegment } from '@/utils/paths/redirects';
import { pickRedirect } from '@/utils/staticProps/pickRedirect';

export function getRedirect(
  path: string,
  redirects?: DocsConfig['redirects'],
  docsNavWithMetadata?: DecoratedNavigationConfig,
  disableRedirect?: boolean
) {
  if (redirects && redirects.length > 0) {
    const explicitRedirect = getExplicitRedirectFromDocsConfig(path, redirects);
    if (explicitRedirect) return explicitRedirect;

    const wildcardRedirect = getWildcardRedirectFromDocsConfig(path, redirects);
    if (wildcardRedirect) return wildcardRedirect;
  }

  if (!docsNavWithMetadata) return;

  return pickRedirect(docsNavWithMetadata, path, disableRedirect);
}

function getExplicitRedirectFromDocsConfig(
  slug: string,
  redirects: NonNullable<DocsConfig['redirects']>
) {
  const source = '/' + slug;
  const redirect = redirects.find((r) => r.source === source && !isWildcardRedirect(r.destination));

  if (redirect == undefined) return;
  return {
    destination: redirect.destination,
    permanent: redirect.permanent ?? true,
  };
}

function getWildcardRedirectFromDocsConfig(
  slug: string,
  redirects: NonNullable<DocsConfig['redirects']>
) {
  const source = `/${slug}`;

  function substituteWildcard(
    destination: string,
    wildcard: string,
    wildcardMatch: string,
    afterString: string
  ) {
    if (isWildcardRedirect(destination))
      return destination.replace(wildcard, wildcardMatch) + afterString;
    return destination;
  }

  const redirectsWithWildcards = redirects.filter((r) => isWildcardRedirect(r.source));

  const destinations: { destination: string; permanent: boolean }[] =
    redirectsWithWildcards.flatMap((redirect) => {
      const permanent = redirect.permanent ?? true;
      const redirectSourceSplit = redirect.source.split('/');

      const wildcards = redirectSourceSplit.filter((segment) => isWildcardRedirectSegment(segment));
      if (wildcards.length > 1) return [];
      const wildcard = wildcards[0]!;

      if (!redirectSourceSplit.includes(wildcard) && isWildcardRedirect(redirect.destination))
        return [];

      const wildcardIndexInRedirectSource = redirect.source.indexOf(wildcard);

      // match before wildcard
      const beforeString = source.slice(0, wildcardIndexInRedirectSource);
      if (beforeString != redirect.source.slice(0, wildcardIndexInRedirectSource)) return [];

      // match after wildcard
      if (redirectSourceSplit.indexOf(wildcard) === redirectSourceSplit.length - 1) {
        // wildcard is the last element in path
        const wildcardMatch = source.slice(beforeString.length);
        return {
          destination: substituteWildcard(redirect.destination, wildcard, wildcardMatch, ''),
          permanent,
        };
      } else {
        // in source, find the after wildcard string from redirect source
        const stringToFindIndex = wildcardIndexInRedirectSource + wildcard.length;
        const stringToFind = redirect.source.slice(stringToFindIndex);

        const afterStringIndex = source.slice(stringToFindIndex).indexOf(stringToFind);
        if (afterStringIndex < 0) return [];

        // get wildcard match, it's between the before string and after string
        const wildcardMatch = source.slice(beforeString.length, afterStringIndex);
        return {
          destination: substituteWildcard(
            redirect.destination,
            wildcard,
            wildcardMatch,
            source.slice(afterStringIndex)
          ),
          permanent,
        };
      }
    });

  return destinations[0];
}
