import { remarkMdxRemoveJs } from '@mintlify/common';
import { serialize, SerializeSuccess } from '@mintlify/mdx';

export const serializeContent = async (
  source?: string
): Promise<SerializeSuccess | string | null> => {
  if (!source) return null;
  let result = null;

  try {
    const serializeResult = await serialize({
      source,
      mdxOptions: {
        remarkPlugins: [remarkMdxRemoveJs],
      },
    });

    if ('error' in serializeResult) {
      throw serializeResult.error;
    }
    result = serializeResult;
  } catch {
    console.log('Error compiling content', source);
    result = source;
  }

  return result;
};
