import { isPage } from '@mintlify/common';
import { replaceSlashIndex } from '@mintlify/common';
import type { DecoratedNavigationPage } from '@mintlify/models';
import { DecoratedNavigationConfig, divisions } from '@mintlify/validation';

export function getFirstPageStartingWith(
  nav: DecoratedNavigationConfig,
  prefix: string
): DecoratedNavigationPage | undefined {
  if (typeof nav !== 'object') return undefined;

  if (isPage(nav) && nav.href.startsWith(prefix)) {
    nav.href = replaceSlashIndex(nav.href);
    return nav;
  }

  if ('pages' in nav) {
    for (const page of nav.pages) {
      if (typeof page === 'object') {
        const pagePath = getFirstPageStartingWith(page, prefix);
        if (pagePath) {
          pagePath.href = replaceSlashIndex(pagePath.href);
          return pagePath;
        }
      }
    }
  }

  if ('groups' in nav) {
    for (const group of nav.groups) {
      if (group.hidden) continue;
      const page = getFirstPageStartingWith(group as DecoratedNavigationConfig, prefix);
      if (page) {
        page.href = replaceSlashIndex(page.href);
        return page;
      }
    }
  }

  for (const key of divisions) {
    if (key in nav) {
      const items = nav[key as keyof typeof nav] as DecoratedNavigationConfig[];
      if (Array.isArray(items)) {
        for (const item of items) {
          if (typeof item === 'object' && 'hidden' in item && item.hidden) continue;
          const page = getFirstPageStartingWith(item, prefix);
          if (page) {
            page.href = replaceSlashIndex(page.href);
            return page;
          }
        }
      }
    }
  }

  return undefined;
}
