import { MINTLIFY_TAILWIND_PREFIX } from '@mintlify/common';
import postcss, { ChildNode, Root } from 'postcss';
import tailwindcss from 'tailwindcss';

const removeDarkClassNamePrefix = (css: string) => {
  return css.replace(/\.mint-dark \*/g, '.dark *');
};

const getAllSelectors = (nodes: ChildNode[] | Root[]): string[] => {
  const selectors: string[] = [];

  for (const node of nodes) {
    if (node.type === 'rule') {
      selectors.push(node.selector);
    } else if (node.type === 'atrule' && node.nodes) {
      selectors.push(...getAllSelectors(node.nodes));
    }
  }

  return selectors;
};

const getContent = (content: string) => {
  const extensions = ['html', 'tsx', 'mdx'];
  return extensions.map((extension) => ({ raw: content, extension }));
};

/**
 * Get the dynamic tailwind css from the content with prefix
 */
export const getDynamicTailwindCss = async ({ content }: { content: string }) => {
  const css = `@tailwind utilities;`;

  const tailwindPlugin = tailwindcss({
    content: getContent(content),
    corePlugins: { preflight: false },
    prefix: MINTLIFY_TAILWIND_PREFIX,
    darkMode: 'class',
  }) as import('postcss').AcceptedPlugin;

  const result = await postcss([tailwindPlugin]).process(css);

  result.css = removeDarkClassNamePrefix(result.css);

  return result.css;
};

/**
 * Get all the tailwind selectors from the content
 */
export const getTailwindSelectors = async ({ content }: { content: string }) => {
  const css = `@tailwind utilities;`;

  const tailwindPlugin = tailwindcss({
    content: getContent(content),
    corePlugins: { preflight: false },
    darkMode: 'class',
  }) as import('postcss').AcceptedPlugin;

  const result = await postcss([tailwindPlugin]).process(css);
  const selectors = getAllSelectors(result.root.nodes);

  return selectors;
};
